.team{
grid-column:  1 / -1;
grid-row:7;
display:grid;
grid-template-columns:1fr;
//sets height of black section header
grid-template-rows:70px minmax(600px, 1fr);
background:$color-primary;
width:100%;
height:100%;

@include text($Open-Sans,$normal,700, $color-team-text);

//team sub-grid media queries
@include responsive(phone){
//grid-template-columns: 1fr 1fr ;
}

@include responsive(tablet){
//grid-template-columns: 1fr 1fr;
}

@include responsive(big-tablet){
//grid-template-columns: 1fr 1fr 1fr 1fr;
}

& > &_header{
grid-column:1 / -1;
grid-row:1;
display:flex;
align-items:center;
justify-content:center;
background:$color-dark-headers;
text-align:center;
@include text-template($small,$font-family-ads, $color-primary);

& > h2{
color:$color-primary;
}
}//end header

//gradient (unused) mixin needs color variable
//$color1: get-color('ceramic', 1);
//$color2: get-color('blackest', 1);

& > .posts{
grid-column:1;
grid-row:2;
display:flex;
flex-wrap:wrap; 
align-content:flex-start;
justify-content:flex-start;
flex-direction: row; 
margin:0px;
height:100%;
width:100%;
padding: 1.5rem 0rem 1rem 0rem;
background:$color-primary;

//person sections
& > section.person{
height:min-content; //grid-template-rows set height
width:100%;
display:grid;
grid-template-columns:1fr;
border-radius: $header-b-rad;
text-align:center;
@include background(none, $color-preview-tags-background, 0.3, none);   

@include responsive(small-phone){
margin: 1rem 1.5rem 1.5rem 1.5rem;
padding:0px;
grid-template-rows:minmax(14rem, auto) minmax(3rem, auto) minmax(2rem, auto) ;
}

@include responsive(galaxy){
grid-template-rows:minmax(13rem, auto) minmax(15rem, auto) minmax(4rem, auto) ;
margin: 1rem .5rem 1.5rem .5rem;
}//361px

@include responsive(galaxy-fold){
margin: 1rem .2rem 1.5rem .2rem;
grid-template-rows:minmax(11rem, auto) minmax(12rem, auto) minmax(2rem, auto) ;
}//280px

//forward cascade
@include responsive(phone){
width:100%;
margin: 1rem 0rem 1.5rem 0rem;
grid-template-rows:minmax(20rem, auto) minmax(18rem, auto) minmax(6rem, auto) ;
}

@include responsive(tablet-min){
grid-template-rows:minmax(20rem, auto) minmax(19rem, auto) minmax(7rem, auto) ;
width:100%;
}//574px x 1.13 = 650px

@include responsive(tablet){
grid-template-rows:18rem 23rem 6rem;
width:47%;
margin: 1rem 1.5% 1.5rem 1.5%;
}

@include responsive(big-tablet){
//grid-template-rows:19rem 27rem 6rem;
grid-template-rows:19rem 24rem 6rem;
width:46%;
margin: 1rem 2% 1.5rem 2%;
}
@include responsive(laptop){
grid-template-rows:19rem 24rem 6rem;
width:43%;
margin: 1rem 3.5% 2.5rem 3.5%;
}
@include responsive(desktop){
width:31.333%;
margin: 1rem 1% 1.5rem 1%;
grid-template-rows:18rem 23rem 6rem;
}

@include responsive(big-desktop){
grid-template-rows:19rem 23rem 6rem;
}

@include responsive(developer-plus){
grid-template-rows:20rem 23rem 6rem;
}

@include responsive(tv){
grid-template-rows:21.5rem 26rem 7rem;
}

@include responsive(big-tv){
grid-template-rows:23rem 27rem 8rem;
}//2250px x 1.111 2500px

& > h4{
color: $color-primary;
}

//appearing stats panel
& > .stats{
grid-column:1;
grid-row:1 / 4;
display: grid;
grid-template-columns:1fr 1fr 1fr;
grid-template-rows:minmax(0rem, auto) minmax(0rem,auto);
align-content:start;
@include background(none, $color-stats-background, 0.85, cover);
box-shadow: 0 48px 48px 5px rgba(0,0,0,1);
border-radius: $header-b-rad;
color: #fff;
opacity: 0;
padding: $pad-normal;
transform-origin: 0 0;
transform: scale(.3, .3);
will-change:transform, opacity;
transition: transform 1s ease-out, opacity .5s ease-out;
height:min-content;
z-index:2;

@include responsive(small-phone){
width:80%;
}//575px

@include responsive(pixel-2){
width:100%;
}//411px

@include responsive(tablet-min){
width:85%;
}//574px x 1.13 = 650px
@include responsive(tablet){
width:100%;
}//650px x 1.18 = 768px

& > div{

& > h3 {
@include text-ph($font-family-ads,$larger,600, $color-primary);
margin: .25rem auto 0rem auto;
padding:.75rem .75rem .75rem .75rem;

@include responsive(galaxy-fold){
margin: .1rem auto 0rem auto;
padding:.2rem .3rem .2rem .3rem;
}//280px

@include responsive(phone){
margin: 1rem auto 0rem auto;
padding:.3rem .4rem .3rem .4rem;
}//500px x 1.15 = 574px

@include responsive(tablet){
@include text-ph($font-family-ads,$large,600, $color-primary);
}//500px x 1.15 = 574px

@include responsive(tv){
padding:.5rem .6rem .7rem .6rem;
@include text-ph($font-family-ads, $med,600, $color-primary);
}//500px x 1.15 = 574px
}//end team member

& > h4 {
@include text-ph($font-family-ads,$larger,600, $color-primary);
padding:.75rem 0rem .75rem 0rem;

@include responsive(galaxy-fold){
padding:.4rem 0rem .4rem 0rem;
}//280px

@include responsive(tablet){
padding:.3rem 0rem .3rem 0rem;
@include text-ph($font-family-ads,$large,600, $color-primary);
}//500px x 1.15 = 574px

@include responsive(tv){
padding:.7rem 0rem .7rem 0rem;
}//500px x 1.15 = 574px
}//end li

& > li {
@include text-ph($font-family-courier,$large,500, $color-primary);
list-style-type: none;
text-align:right;
margin:0rem .5rem 0rem 0rem;
padding:.15rem 0rem;

@include responsive(galaxy-fold){
padding: .05rem 0rem;
margin:0rem .2rem 0rem 0rem;
}//280px

@include responsive(tablet){
@include text-ph($font-family-courier,$big-plus,500, $color-primary);
}//650px x 1.18 = 768px

@include responsive(desktop){
@include text-ph($font-family-courier,$big,500, $color-primary);
}//650px x 1.18 = 768px

@include responsive(tv){
@include text-ph($font-family-courier,$less-big,500, $color-primary);
}//650px x 1.18 = 768px
}//end li

& > span{
grid-column:1/3;
@include text-ph($font-family-courier,$large,500, $color-primary);
margin:0rem 0rem 0rem 0rem;
padding:.15rem 0em;
align-self:start;
justify-self:center;

@include responsive(galaxy-fold){
padding: .05rem 0rem;
margin:0rem 0rem 0rem 0rem;
}//280px

@include responsive(tablet){
@include text-ph($font-family-courier,$big-plus,500, $color-primary);
}//650px x 1.18 = 768px

@include responsive(desktop){
@include text-ph($font-family-courier,$big,500, $color-primary);
}//650px x 1.18 = 768px

@include responsive(tv){
@include text-ph($font-family-courier,$less-big,500, $color-primary);
}//650px x 1.18 = 768px
}//end : span

& > p {
@include text-ph($font-family-courier,$large,500, $color-primary);
list-style-type: none;
text-align:left;
margin:0rem 0rem 0rem .5rem;
padding:.15em 0em;

@include responsive(galaxy-fold){
padding: .05rem 0rem;
margin:0rem 0rem 0rem .2rem;
}//280px

@include responsive(tablet){
@include text-ph($font-family-courier,$big-plus,500, $color-primary);
}//650px x 1.18 = 768px

@include responsive(desktop){
@include text-ph($font-family-courier,$big,500, $color-primary);
}//650px x 1.18 = 768px

@include responsive(tv){
@include text-ph($font-family-courier,$less-big,500, $color-primary);
}//650px x 1.18 = 768px
}//end p

}

& > .team_id{
grid-column:2/4;
grid-row:1/2;
display:grid;
grid-template-columns:1fr; 
grid-template-rows:auto;
width:100%;
height:min-content;
align-content: start;

& > .row1{
grid-column:1;
grid-row:1;
background: $color-stats-title; 
