}//650px x 1.18 = 768px

@include responsive(tv){
padding:1.1rem 0rem 1rem 0rem;
}//650px x 1.18 = 768px

@include responsive(big-tv){
padding:1.1rem 0rem 1.5rem 0rem;
}//2250px x 1.111 2500px
}//end name

}//close team_id

