//text appears
& > h3:hover h3, & > li:hover {
opacity: 1;
}

& > h3 {
}

&.is-hovered{
opacity:1;
box-shadow: 0 48px 48px 5px rgba(0,0,0,1);
transform: none;
}
} //end stats

& > picture.image-holder{
grid-column:1;
grid-row:1;
display:flex;
height:100%;
width:100%;
position:relative;

& > .img{
display:flex;
top: 50%;
left: 50%;
position: absolute;
margin: 0rem auto;
transform: translate(-50%, -50%) scale(1.5);
will-change: transform;
border-radius:$rad-big;
background: $color-team-image-background; 
height:4rem;
width:4rem;

@include responsive(small-phone){
height:8rem;
width:8rem;
}
@include responsive(phone-500){
transform: translate(-50%, -50%) scale(1.6);
height:6.5rem;
width:6.5rem;
}
@include responsive(pixel-2){
height:6rem;
width:6rem;
}
@include responsive(iphone-x){
transform: translate(-50%, -50%) scale(1.6);
height:5.8rem;
width:5.8rem;
}
@include responsive(galaxy){
height:6rem;
width:6rem;
transform: translate(-50%, -50%) scale(1.5);
}//361px

@include responsive(iphone-5){
transform: translate(-50%, -50%) scale(1.7);
height:4.9rem;
width:4.9rem;
}
@include responsive(galaxy-fold){
height:4.5rem;
width:4.5rem;
transform: translate(-50%, -50%) scale(1.5);
}

@include responsive(phone){
transform: translate(-50%, -50%) scale(1.8);
height:7.3rem;
width:7.3rem;
}
@include responsive(tablet){
transform: translate(-50%, -50%) scale(1.7);
height:5.5rem;
width:5.5rem;
}
@include responsive(big-tablet){
height:6.5rem;
width:6.5rem;
transform: translate(-50%, -50%) scale(1.65);
}
@include responsive(laptop){
height:6.5rem;
width:6.5rem;
}
@include responsive(desktop){
transform: translate(-50%, -50%) scale(1.8);
height:6.5rem;
width:6.5rem;
}
@include responsive(big-desktop){
transform: translate(-50%, -50%) scale(1.7);
height:7rem;
width:7rem;
}
@include responsive(developer-plus){
transform: translate(-50%, -50%) scale(1.8);
height:7.5rem;
width:7.5rem;
}//end developer-plus

@include responsive(tv){
transform: translate(-50%, -50%) scale(1.6);
height:8.5rem;
width:8.5rem;
}//end developer-plus

@include responsive(mid-tv){
transform: translate(-50%, -50%) scale(1.6);
height:9.5rem;
width:9.5rem;
}//2000px x 1.125 = 2250px

@include responsive(big-tv){
transform: translate(-50%, -50%) scale(1.6);
height:10rem;
width:10rem;
}//2000px x 1.125 = 2250px
}//end image
}// image-holder

& > .image-holder {

& > .img-hover-one, & > .img-hover-two, & > .img-hover-three, & > .img-hover-four, & > .img-hover-five, & > .img-hover-six {

&.is-hovered{
