& > .stats:hover{
opacity:1;
box-shadow: 0 48px 48px 5px rgba(0,0,0,1);
transform:none;
}

& > .bio{
grid-column:1;
grid-row:2;
height:min-content;
width:100%;
overflow:hidden;

& > h4{
@include text-ph($font-family-nav, $supersized, 700, $color-team-bio-headers);
margin:0rem 0rem .7rem 0rem;

@include responsive(galaxy-fold){
margin:0rem 0rem .4rem 0rem;
}//280px

@include responsive(tablet){
@include text-ph($font-family-nav, $massive,700, $color-team-bio-headers);
padding: 0rem 0.75rem 0rem 0.75rem;
}//650px x 1.18 = 768px

@include responsive(desktop){
@include text-ph($font-family-nav, $jumbo,700, $color-team-bio-headers);
}//650px x 1.18 = 768px
}//end h4

& > h6{
@include text-ph($font-family-nav, $massive,700, $color-team-bio-headers);
margin:0rem 0rem 1rem 0rem;

@include responsive(galaxy){
margin:0rem 0rem .5rem 0rem;
}//361px
@include responsive(galaxy-fold){
margin:0rem 0rem .3rem 0rem;
}//280px

@include responsive(tablet){
@include text-ph($font-family-nav, $larger,700, $color-team-bio-headers);
padding: 0rem 0.75rem 0rem 0.75rem;
}//650px x 1.18 = 768px

@include responsive(desktop){
@include text-ph($font-family-nav, $large,700, $color-team-bio-headers);
}//650px x 1.18 = 768px
}//end h6

& > p{
@include text-ph($font-family-ads, $jumbo, 400, $color-team-bio-text);
line-height:26px;

@include responsive(small-phone){
padding: 0.75rem 0.75rem 0.75rem 0.75rem;
}//575px
@include responsive(galaxy){
padding: 0.25rem 0.75rem 0.25rem 0.75rem;
line-height:22px;
}//361px

@include responsive(galaxy-fold){
padding: 0rem 0.5rem;
}//280px

@include responsive(phone){
padding: 0.75rem 1.5rem 0.75rem 1.5rem;
line-height:2rem;
}

@include responsive(tablet-min){
padding: 0.75rem 2rem 0.75rem 2rem;
line-height:2rem;
}//574px x 1.13 = 650px

@include responsive(tablet){
@include text-ph($font-family-ads, $med, 400, $color-team-bio-text);
padding: 1rem .75rem 0.5rem .75rem;
}//650px x 1.18 = 768px

@include responsive(big-tablet){
padding: 1rem 1rem 0.5rem 1rem;
}//650px x 1.18 = 768px

@include responsive(desktop){
@include text-ph($font-family-ads, $big-plus, 400, $color-team-bio-text);
//padding: 1rem 1.3rem 0.5rem 1.3rem;
}//1030px x 1.16 = 1200px

@include responsive(big-desktop){
padding: 1rem 1.3rem 0.5rem 1.3rem;
}//1030px x 1.16 = 1200px

@include responsive(developer-plus){
padding: 1rem 1.5rem 0.5rem 1.5rem;
}//1030px x 1.16 = 1200px

@include responsive(tv){
line-height:2.4rem;
padding: 1.2rem 1.7rem 0.6rem 1.7rem;
}//1030px x 1.16 = 1200px
}//end paragraph (bio text <p>)
}//end bio

& > ul{
grid-column:1;
grid-row:3;
display:flex;
flex-direction:row;
justify-content:center;
align-content:center;
margin:1rem 0rem 0rem 0rem;
height:min-content;

@include responsive(small-phone){
margin:1rem 0rem 0rem 0rem;
}//575px

@include responsive(galaxy){
margin:.5rem 0rem 0rem 0rem;
}//361px

@include responsive(galaxy-fold){
margin:.25rem 0rem 0rem 0rem;
}//280px

& > li{
border:none;
@include text-ph(none,$supersized,700, none);
height:min-content;
padding: .75rem 1.25rem;
margin:0rem;

@include responsive(small-phone){
padding: .75rem .9rem;
}//575px

@include responsive(galaxy){
padding: 0rem .75rem;
}//361px

